import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import { ServicosProvider } from '../../providers/servicos/servicos';

@IonicPage()
@Component({
  selector: 'page-endereco-cobranca',
  templateUrl: 'endereco-cobranca.html',
})
export class EnderecoCobrancaPage {

  model = {
    rua: "",
    numero: "",
    complemento: "",
    referencia: "",
    cep: "",
    cidade: "",
    estado: "",
    mesmoEnderecoCobranca: "S"
  }  

  constructor(public navCtrl: NavController, public navParams: NavParams, private database: AngularFireDatabase, public serv: ServicosProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EnderecoCobrancaPage');
  }

  salvarDados(){

    this.database.list('enderecoCobranca')
      .push({ 
          clienteId: this.serv.usuarioLogado.clienteId,
          rua: this.model.rua,
          numero: this.model.numero,
          complemento: this.model.complemento,
          referencia: this.model.referencia,
          cep: this.model.cep,
          cidade: this.model.cidade,
          estado: this.model.estado
      }).then(() => {

          if(this.serv.usuarioLogado.cartaoCredito.numero == ""){
            this.navCtrl.push('CartaoCreditoPage');
          }else{
            this.navCtrl.push('FecharPedidoPage');
          }
        });
  }  

}



